.root {
    padding: 0 16px;

    .logoBox {
        width: 100px;
        height: 100px;
        border-radius: 50%;
        overflow: hidden;
        margin: 0 auto;
        position: relative;
        //box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);

        > img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        .logoBorder {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            border-radius: 50%;
            border: 4px solid #fff;
            pointer-events: none;
        }
    }

    .content {
        justify-content: center;
        display: flex;
        flex-direction: column;
        flex: 1 1 0;
        width: 100%;
        text-align: center;

        > h1 {
            font-weight: 700;
            font-size: 24px;
            line-height: 1.2;
            margin: 16px 0 0 0;
        }

        > p {
            margin: 4px 0 0 0;
            font-weight: 400;
            font-size: 16px;
        }
    }

    &.left, &.right {
        display: flex;
        flex-direction: row;
        align-items: center;

        .logoBox {
            margin: 0 16px 0 0;
        }

        .content {
            align-items: flex-start;

            > h1 {
                margin-top: 0;
            }
        }
    }

    &.right {
        flex-direction: row-reverse;

        .logoBox {
            margin: 0 0 0 16px;
        }
    }
}
