@import 'vars';

::-moz-selection {
    background: #333;
    color: #fff;
}
::selection {
    background: #333;
    color: #fff;
}

html {
    font-family: $font-family;
    font-size: $font-size;
    line-height: 1.5;
    color: $font-default-color;
    width: 100%;
    height: 100%;
}

body {
    background-color: #f7f7fa;
    width: 100%;
    min-height: 100%;

    transition: background-color $default-transition;
}

* {
    box-sizing: border-box;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
    color: $link-color;
    text-decoration: none;
    font-weight: 500;
}

p {
    font-size: 1em;
    margin: 1em 0;
}

:global(.grecaptcha-badge) {
    visibility: hidden;
}

:global(#mssgme_widget) {
    //display: none;
    bottom: 60px;
}

@media all and (max-width: $mobile-breakpoint) {
    $circle-offset: 10px;
    $popup-offset: $circle-offset + 71px;
    $popup-height: $popup-offset + 10px;

    :global(#widget_circle) {
        bottom: $circle-offset !important;
    }

    :global(#mssg_widget_box) {
        bottom: $popup-offset !important;
        max-height: calc(100% - #{$popup-height}) !important;
    }
}

:global(#mssgme_widget.visible) {
    //@media all and (max-width: $mobile-breakpoint) {
    display: block !important;
    //}
}
