@import 'vars';

.root {
    width: 100%;
    height: 160px;
    overflow: hidden;
    position: relative;
    //box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
    transition: height $default-transition;


    @media all and (min-width: $tablet-portrait-breakpoint) {
        height: 285px;
    }

    &.mobileMode {
        height: 160px;
    }

    > img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}
