.compact {
    .paneWrapper {
        width: 100%;
        padding: 0 16px;
        position: relative;

        &.withCover {
            margin: -16px 0 0 0;
        }

        &.withPadding {
            margin-top: 16px;
        }
    }

    .pane {
        display: flex;
        width: 100%;
        //box-shadow: 0 0 16px rgba(0, 0, 0, 0.1);
        border-radius: 16px;
        background-color: #fff;
        padding: 6px;
        min-height: 72px;
    }

    .logoBox {
        width: 60px;
        height: 60px;
        border-radius: 14px;
        overflow: hidden;
        flex-shrink: 0;

        > img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .contentWrapper {
        display: flex;
        width: 100%;
        align-items: center;
        flex-wrap: wrap;
    }

    .content {
        width: 100%;
        padding: 0 10px;

        > h1 {
            font-weight: bold;
            font-size: 20px;
            line-height: 1.1;
            margin: 0;
        }

        > p {
            margin: 0;
            font-size: 14px;
        }
    }

    &.center {
        .paneWrapper {
            &.withCover {
                margin: -69px 0 0 0;
            }
        }

        .pane {
            flex-direction: column;
            align-items: center;

            .content * {
                text-align: center;
            }

            .logoBox + .contentWrapper {
                margin-top: 10px;
            }
        }
    }
}
