.centered {
    .logoBox {
        width: 100px;
        height: 100px;
        border-radius: 50%;
        overflow: hidden;
        border: 3px solid #fff;
        margin: -50px auto 0 auto;
        position: relative;
        //box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);

        > img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .content {
        justify-content: center;
        display: flex;
        flex-direction: column;
        flex: 1 1 0;
        width: 100%;
        text-align: center;

        > h1 {
            font-weight: bold;
            font-size: 26px;
            line-height: 1.1;
            margin: 8px 0 0 0;
        }

        > p {
            margin: 0;
            font-size: 14px;
        }
    }
}
