.centered {
    .panelWrapper {
        display: block;
        margin-top: 16px;

        .logoBox {
            width: 100px;
            height: 100px;
            border-radius: 50%;
            overflow: hidden;
            border: 4px solid #fff;
            margin: 0 auto 0 auto;
            position: relative;
            //box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);

            > img {
                display: block;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        .content {
            justify-content: center;
            display: flex;
            flex-direction: column;
            flex: 1 1 0;
            width: 100%;
            text-align: center;

            > h1 {
                font-weight: bold;
                font-size: 26px;
                line-height: 1.1;
                margin: 6px 0 0 0;
                word-break: break-word;
            }

            > p {
                margin: 0;
                font-size: 14px;
                word-break: break-word;
            }
        }

        margin-left: 16px;
        margin-right: 16px;

        &.center {
            &.withCover {
                margin-top: -50px;
            }
        }

        &.left,
        &.right {
            display: flex;

            &.withCover {
                margin-top: -20px;
            }

            .logoBox {
                width: 90px;
                height: 90px;
                flex-shrink: 0;
            }

            .content {
                text-align: left;
                width: calc(100% - 122px);

                > h1 {
                    width: 100%;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    font-size: 24px;
                    margin: 20px 0 0 0;
                }

                > p {
                    width: 100%;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
            }
        }

        &.left {
            .content {
                padding-left: 12px;
            }
        }

        &.right {
            flex-direction: row-reverse;

            .content {
                padding-right: 12px;
            }
        }
    }
}
