.root {
    display: flex;
    width: 100%;
    height: 56px;
    align-items: center;
    justify-content: center;
    color: #fff;
    background-color: #2f3542;
    user-select: none;
    overflow: hidden;
    transition: box-shadow 0.3s ease, transform 0.3s ease;
    font-weight: 500;
    font-size: 16px;
    line-height: 56px;
    text-align: center;
    text-transform: capitalize;
    letter-spacing: 0.02px;
    cursor: pointer;

    @media all and (min-width: 1025px) {
        &:hover {
            box-shadow: 0 10px 10px -6px rgba(0, 0, 0, 0.24);
            transform: translateY(-2px);
        }
    }

    > img,
    > svg {
        fill: currentColor;
        display: inline-block;
        width: 21px;
        height: auto;
        vertical-align: top;
        margin: 0;
        pointer-events: none;
        position: relative;
    }

    > span {
        display: inline-block;
        vertical-align: top;
        margin: 0 0 0 8px;
        pointer-events: none;
        position: relative;
    }
}

.messenger {
    background-image: linear-gradient(to bottom, #00b1ff, #006dfe);

    > span::before {
        content: 'Messenger';
    }
}

.whatsapp {
    background-image: linear-gradient(to bottom, #60fd7c, #07b825);

    > span::before {
        content: 'WhatsApp';
    }
}

.telegram {
