.vk {
    background-image: linear-gradient(to bottom, #66a2e7, #3874b9);

    > span::before {
        content: 'Vkontakte';
    }
}

.skype {
    background-image: linear-gradient(to bottom, #00bbf2, #007ad4);

    > span::before {
